import "./Card.css";

function Card(props) {

  return (
    <div class="card">
      <img src={`/images/${props.imageFile}`} alt="Avatar" style={{width: '100%'}} />
      <div class="container">
        <h4><b>{props.name}</b></h4> 
        <p>{props.description}</p> 
      </div>
    </div>
  );

}

export default Card;