import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import { useState } from 'react';
import Card from "./Card/Card.js";
import Grid from "./Grid/Grid.js";

function MeetTheCrew() {

  const [menuSelection, setMenuSelection] = useState(0);

  const gridContent = (
    <div>
      <Link className="x1y1" to="/meet-the-crew/lawson">
        <Card name="Lawson" description="DM" imageFile="img_avatar.png" />
      </Link>
      <Link className="x1y2" to="/meet-the-crew/chris">
        <Card name="Chris" description="Player" imageFile="img_avatar.png" />
      </Link>
      <Link className="x1y3" to="/meet-the-crew/fabbs">
        <Card name="Fabbs" description="Player" imageFile="img_avatar.png" />
      </Link>
      <Link className="x2y1" to="/meet-the-crew/hailey">
        <Card name="Hailey" description="Player" imageFile="img_avatar.png" />
      </Link>
      <Link className="x2y2" to="/meet-the-crew/luke">
        <Card name="Luke" description="Player" imageFile="img_avatar.png" />
      </Link>
      <Link className="x2y3" to="/meet-the-crew/oscar">
        <Card name="Oscar" description="Player" imageFile="img_avatar.png" />
      </Link>
    </div>
  );

  return (
    <div>
      <div className="jumbotron jumbotron-fluid vh-100 text-center d-flex flex-column justify-content-center bg-secondary py-5">
        <div className="container">
          <div className="jumbotron jumbotron-fluid bg-light py-5">
            <h1 class="display-4">Meet The Crew</h1>
            <p className="lead">Get to know us.</p>
            <Grid columnCount={3} content={gridContent} />
          </div>
        </div>
      </div>
    </div>
  );

}

export default MeetTheCrew;